import React from "react";


const nav = () => {
  return (
    <>
      <nav
        className="navbar navbar-expand-lg bg-body-tertiary fixed-top"data-aos="zoom-out-down"
        data-aos-duration="3000"
        style={{
          backgroundImage:
            "url('https://milliondollarads.com/hosted/images/c7/3001968a264fbfa68982cf31e64f28/MDAheaderBackgroundBlue2-10.png')",
        }}
      >
        <div className="container-fluid">
          <img
            src="//milliondollarads.com/hosted/images/a0/10f275712147eab8743b0d2cd17ba3/MDAlogoALLWHITE-03.png"
          
            alt=""
            width="200"
            data-src="//milliondollarads.com/hosted/images/a0/10f275712147eab8743b0d2cd17ba3/MDAlogoALLWHITE-03.png"
            tabindex="0"
          />

          <button
            style={{ backgroundColor: "white" }}
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNavAltMarkup"
            aria-controls="navbarNavAltMarkup"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"   style={{color: "white" }} />
          </button>
          <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
            <div className="navbar-nav ms-auto">
              <a
                className="nav-link "
                aria-current="page"
                href="#"
                style={{ color: "white" }}
              >
                Order
              </a>
              <a className="nav-link" href="#" style={{ color: "white" }}>
                Login
              </a>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default nav;
